import { Main } from '../../component/layout/main';
import Title from '../../component/title';
import { ProgressBar } from '../../component/progress';
import { connect } from 'react-redux';
import moment from 'moment';
import useItem from '../../hooks/useItem';
import { apiGetRequestByIdStudent } from '../../services/serviceRequest';
import { LoadingSpinner } from '../../component/spinner';
import { CSSTransition } from 'react-transition-group';
import { signOut } from '../Login/actions/auth.actions';

const ViewRequest = ({ request, id, signOut }) => {
  const { item, loading, success } = useItem({
    id,
    serviceApi: apiGetRequestByIdStudent,
    signOut,
  });
  return (
    <Main limit="limit">
      <Title title="Solicitud" back={true} />
      <div className="container-page">
        {loading && <LoadingSpinner />}
        {success && (
          <CSSTransition
            in={success}
            classNames="fade"
            unmountOnExit
            timeout={400}
          >
            <div className="card-request">
              <div className="request-header">
                <div className="name">Nombre</div>
                <div className="description">{`${item.name_student} ${item.surname_student}`}</div>

                <div className="carne">
                  <div className="name">Carn√©</div>
                  {`${item.carne_student}`}
                </div>
              </div>
              <div className="request-body">
                <div className="name">
                  Proyecto <div>{moment(item.date).format('LLLL')}</div>
                </div>
                <div className="title">{item.name}</div>
                <div className="description">{item.description}</div>
                <div className="status">
                  <ProgressBar status={item.approved} />
                  <strong>{item.approved.toUpperCase()}</strong>
                </div>
              </div>
            </div>
          </CSSTransition>
        )}
        {Object.keys(item).length === 0 && (
          <CSSTransition
            in={true}
            classNames="fade"
            unmountOnExit
            timeout={400}
          >
            <div className="empty">
              <div style={{ textAlign: 'center' }}>
                <img
                  src="https://carrerainglesuce.files.wordpress.com/2019/05/papers.gif"
                  alt=""
                  width={200}
                />
                <div className="description">
                  Aun no tienes una solicitud de adjudicaci√≥n.
                </div>
              </div>
            </div>
          </CSSTransition>
        )}
      </div>
    </Main>
  );
};

const mapStateToProps = (state) => ({
  request: state.PageReducer.data,
  id: state.AuthReducer.userInfo.id,
});

const mapDispatchToProps = (dispatch) => ({
  signOut: () => dispatch(signOut()),
});

export default connect(mapStateToProps, mapDispatchToProps)(ViewRequest);
