import * as Actions from '../pages/actions/page.actions';

const initialState = {
  loading: false,
  data: null,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case Actions.LOADING: {
      return {
        ...state,
        loading: action.payload,
      };
    }
    case Actions.SET_DATA: {
      return {
        ...state,
        data: action.payload,
      };
    }
    default:
      return state;
  }
};

export default reducer;
