import styled from 'styled-components';

const CardCustom = styled.div`
  box-shadow: ${(props) => (props.color ? props.color : 'rgb(0 0 0 / 18%)')} 0px
    0.063rem ${(props) => (props.intensity ? '0.3rem' : '0.1rem')} 0px;
  width: 100%;
  height: ${(props) => props.height};
  min-height: 100px;
  background-color: #fff;
  border-radius: 5px;
  padding: ${(props) => (props.padding ? props.padding : '25px 20px')};
  position: relative;
  display: ${ props => props.display || 'grid'};
  color: ${(props) => props.fontColor || '#707070'};
  overflow: hidden;
  transition: all 250ms ease-in-out;
  cursor: ${(props) => props.cursor};
  border-top: ${(props) =>
    props.borderTop ? `${props.sizeBorder}px ${props.colorBorder} solid` : ''};
  @media (max-width: 768px){
    padding: 25px 25px;
  }
  &:hover {
    box-shadow: ${(props) =>
        props.color ? props.color : 'rgb(60 60 157 / 69%)'}
      0px 0.063rem 0.5rem 0px;
    border-top: ${(props) =>
      props.borderTop ? `${props.sizeBorder}px ${props.colorHover} solid` : ''};
  }

  svg {
    transition: all 250ms ease-in-out;
  }
  &:hover svg {
    color: #3c3c9ddb;
  }
`;

export const Card = (props) => {
  return <CardCustom {...props}>{props.children}</CardCustom>;
};
