import { TextField } from '@mui/material';
import { Fragment } from 'react';

export const InputTextForm = ({
  title,
  name,
  type,
  onChange,
  value,
  placeholder,
  description,
  required,
  multiline,
  rows,
  maxRows,
  readOnly,
}) => {
  const inputStyle = {
    margin: '10px 0px 0px 0px',
    borderRadius: '50px',
    width: '100%',
    color: 'black',
  };

  return (
    <Fragment>
      <div className="input">
        <label className="input-label">{title}</label>
        <TextField
          className={readOnly ? 'input-readonly' : ''}
          style={inputStyle}
          variant="outlined"
          name={name}
          type={type}
          onChange={onChange}
          value={value}
          size="small"
          placeholder={placeholder}
          required={required}
          multiline={multiline}
          minRows={rows}
          maxRows={maxRows}
          InputProps={{
            readOnly: readOnly,
            disabled: readOnly,
          }}
        />
        {description && (
          <small className="input-description">{description}</small>
        )}
      </div>
    </Fragment>
  );
};
