import React from 'react';
import { connect } from 'react-redux';
import { useRouteMatch } from 'react-router-dom';
import { Route, Switch } from 'react-router-dom';
import { NotFound } from '../NotFound';

import Home from './home';
import NewRequests from './newRequest';
import ViewRequest from './viewRequest';

const HomeStudent = ({ loggedIn }) => {
  const { path } = useRouteMatch();
  return (
    <Switch>
      <Route exact path={path} component={Home} />
      <Route path={`${path}/new`} component={NewRequests} />
      <Route path={`${path}/view`} component={ViewRequest} />
      <Route path="*" component={NotFound} />
    </Switch>
  );
};

const mapStateToProps = (state) => ({
  loggedIn: state.AuthReducer.userInfo.loggedIn,
  role: state.AuthReducer.role,
});
export default connect(mapStateToProps, null)(HomeStudent);
