import { Fragment } from 'react';
import { Button } from '../button/button';

export const FormAction = ({
  children,
  onSubmit,
  onCancel,
  onClick,
  loading,
}) => {
  return (
    <Fragment>
      <div className="form-action">
        {onSubmit && (
          <Button
            name="submit"
            type="submit-form"
            text="Ingresar"
            color="primary"
            variant="contained"
            action="submit"
            loading={loading}
          />
        )}
        {onCancel && (
          <Button
            type="submit-form"
            text="Ingresar"
            color="primary"
            variant="contained"
          />
        )}

        {onClick && (
          <Button
            type="submit-form"
            text="Ingresar"
            color="primary"
            variant="contained"
            onClick={onClick}
          />
        )}
      </div>
    </Fragment>
  );
};
