import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';

const CustomTitle = styled.h3`
  width: 100%;
  font-weight: 600;
  line-height: 1.2;
  font-size: x-large;
  h3 {
    color: ${(props) => props.color || 'black'};
  }
`;
const Title = ({ title, color, size, colorBorder, marginBottom, back }) => {
  const history = useHistory();
  const handleChangeHistory = () => {
    history.goBack();
  };
  return (
    <div style={{ display: 'flex', gap: '12px', justifyContent: 'center', alignItems: 'center' , borderBottom: '0.1px #3c3c9d52 solid', marginBottom, padding:'18px 0'}}>
      {back && (
        <div className="icon" onClick={handleChangeHistory}>
          <ArrowBackIosIcon />
          <h3>Regresar</h3>
        </div>
      )}
      <CustomTitle marginBottom={marginBottom}>{title}</CustomTitle>
    </div>
  );
};

export default Title;
