import { Grid } from '@mui/material';
import { Redirect, useHistory, useParams } from 'react-router-dom';
import { Main } from '../../component/layout/main';
import Title from '../../component/title';
import Face from '../../assets/images/faces.gif';
import { Button } from '../../component/button';
import moment from 'moment';
import useItem from '../../hooks/useItem';
import { apiGetRequestById } from '../../services/serviceRequest';
import { CSSTransition } from 'react-transition-group';
import { LoadingSpinner } from '../../component/spinner';
import useRequest from '../../hooks/useRequest';
import { AlertSuccess } from '../../component/alert';
import { signOut } from '../Login/actions/auth.actions';
import { connect } from 'react-redux';

const CheckRequest = ({ signOut }) => {
  const { id } = useParams();
  const history = useHistory();
  const { item, loading, success } = useItem({
    id,
    serviceApi: apiGetRequestById,
  });

  const servicesRequest = useRequest();

  const onApproved = () => {
    servicesRequest.updateRequest({
      request: { ...item, approved: 'aprobado' },
      id,
      signOut,
    });
  };

  const onReject = () => {
    servicesRequest.updateRequest({
      request: { ...item, approved: 'rechazado' },
      id,
      signOut,
    });
  };

  return (
    <Main limit="limit">
      <Title
        title={`Vista de solicitud No. ${id}`}
        marginBottom="35px"
        back={true}
      />

      {loading && <LoadingSpinner />}
      {success && (
        <CSSTransition
          in={success}
          timeout={600}
          unmountOnExit
          classNames="fade"
        >
          <div className="container-page">
            <div className="request">
              <div className="request-header">
                <div className="request-wrap">
                  <div className="request-container">
                    <div className="request-info">
                      <div className="date">
                        {moment(item.date).format('LLLL')}{' '}
                      </div>
                      <div className="title">
                        <h3>{item.name}</h3>
                      </div>
                      <div className="request-info-body">
                        <div className="info-student">
                          <div className="name">
                            Autor{' '}
                            <strong>
                              <i>
                                {item.name_student} {item.surname_student}{' '}
                              </i>
                            </strong>
                          </div>
                          <div className="carne">{item.carne_student}</div>
                          <div className="career">
                            Estudiante de ingenier√≠a en sistemas de informaci√≥n
                            y ciencias de la computaci√≥n.
                          </div>
                        </div>
                        <div className="info-sede">La Antigua Guatemala.</div>
                      </div>
                    </div>
                    <div className="request-avatar">
                      <img src={Face} alt="face" width={400} />
                    </div>
                  </div>
                  <div className="request-body">
                    <div className="title">
                      <h3>Descripci√≥n</h3>
                    </div>
                    <div className="description">
                      <p>{item.description}</p>
                    </div>
                  </div>
                  <div className="request-footer">
                    <span>Universidad Mariano G√°lvez de Guatemala</span>
                  </div>
                </div>
              </div>
            </div>

            <div className="request-actions">
              {!servicesRequest.success && (
                <div className="card-actions">
                  <div className="title">Acciones</div>
                  <div className="actions">
                    <Button
                      text="Aprobar"
                      type="submit-form"
                      variant="contained"
                      color="success"
                      onClick={onApproved}
                    />
                    <Button
                      text="Rechazar"
                      type="submit-form"
                      variant="contained"
                      color="secondary"
                      onClick={onReject}
                    />
                    <Button
                      text="Cancelar"
                      type="submit-form"
                      variant="outlined"
                      color="primary"
                      onClick={() => history.push('/solicitudes/teacher')}
                    />
                  </div>
                </div>
              )}
              <br />
              <AlertSuccess
                description={'Se ha actualizado correctamente el registro.'}
                open={servicesRequest.success}
              />
            </div>
          </div>
        </CSSTransition>
      )}
    </Main>
  );
};

const mapDispatchToProps = (dispatch) => ({
  signOut: () => dispatch(signOut()),
});

export default connect(null, mapDispatchToProps)(CheckRequest);
