import axios from 'axios';
import { API_REST } from '../util/const';

export const apiSaveRequest = async ({ request }) => {
  const data = axios.post(`${API_REST}/solicitude/save`, request);
  return (await data).data;
};

export const apiGetRequestByIdStudent = async ({ id }) => {
  const data = axios.get(`${API_REST}/solicitude/search/student/${id}`);
  return (await data).data;
};

export const apiGetRequestByIdTeacher = async ({ id }) => {
  const data = axios.get(`${API_REST}/solicitude/search/professor/${id}`);
  return (await data).data;
};

export const apiGetRequestById = async ({ id }) => {
  const data = axios.get(`${API_REST}/solicitude/search/${id}`);
  return (await data).data;
};

export const apiUpdateRequestById = async ({ info, id }) => {
  const data = axios.put(`${API_REST}/solicitude/update/${id}`, info);
  return (await data).data;
};
