import { Fragment } from 'react';
import { makeStyles, styled } from '@mui/styles';
import { FormControl, OutlinedInput, Select as MuiSelect } from '@mui/material';
import MenuItem from '@mui/material/MenuItem';
import TextField from '@mui/material/TextField';

const useOutlinedInputStyles = makeStyles((theme) => ({
  root: {
    '& $notchedOutline': {
      borderColor: '#dbdbdb8c',
    },
  },
  focused: {},
  notchedOutline: {},
}));
const TextFileCustom = styled(TextField)({
  '& label.Mui-focused': {
    color: '#fff',
  },
  '& .MuiInput-underline:after': {
    borderBottomColor: '#fff',
  },
  '& .MuiOutlinedInput-root': {
    '& fieldset': {
      borderColor: '#dbdbdb8c',
    },
    '&:hover fieldset': {
      borderColor: '#41436a',
    },
    '&.Mui-focused fieldset': {
      borderColor: '#fff',
    },
  },
});

const inputStyle = {
  margin: '10px 0px 0px 0px',
  borderRadius: '50px',
  width: '100%',
  color: '#fff',
  '& input ': {
    borderRadius: '50px',
  },
};

export const InputText = ({
  title,
  name,
  type,
  onChange,
  value,
  placeholder,
  description,
  required,
  typeInput,
  color,
}) => {
  const outlinedInputClasses = useOutlinedInputStyles();

  const WhatInput = () => {
    switch (typeInput) {
      case 'select': {
        return (
          <FormControl fullWidth size="small">
            <MuiSelect
              value={value}
              onChange={onChange}
              displayEmpty
              inputProps={{ 'aria-label': 'Without label', required: true }}
              required={true}
              input={<OutlinedInput classes={outlinedInputClasses} />}
              sx={{ color: '#fff' }}
              name={name}
              id={name}
            >
              <MenuItem value="" disabled>
                <em>Seleccionar *</em>
              </MenuItem>
              <MenuItem value={3} id={3}>
                Estudiante
              </MenuItem>
              <MenuItem value={2} id={2}>
                Docente
              </MenuItem>
              <MenuItem value={1}>Administrador</MenuItem>
            </MuiSelect>
          </FormControl>
        );
      }
      case 'input': {
        return (
          <TextFileCustom
            style={inputStyle}
            variant="outlined"
            name={name}
            type={type}
            onChange={onChange}
            value={value}
            size="small"
            placeholder={placeholder}
            required={required}
          />
        );
      }
      default:
        return null;
    }
  };

  return (
    <Fragment>
      <div className={`input ${color}`}>
        <label className="input-label">{title}</label>
        <WhatInput />
        {description && (
          <small className="input-description">{description}</small>
        )}
      </div>
    </Fragment>
  );
};
