import { Fragment, useEffect, useState } from 'react';
import { InputTextForm } from '../input/input-form';

import { Button } from '../button/index';
import { Grid } from '@mui/material';

import { connect } from 'react-redux';
import { AlertError } from '../alert/index';
import { useHistory } from 'react-router-dom';
import Autocomplete from '../input/autocomplete';
import useRequest from '../../hooks/useRequest';
import { setData } from '../../pages/actions/page.actions';
import { signOut } from '../../pages/Login/actions/auth.actions';

const FormNewRequest = ({
  id,
  match,
  items,
  setData,
  name,
  surname,
  carne,
  signOut,
}) => {
  const { loading, message, error, saveRequest, showInfo, request } =
    useRequest();

  const history = useHistory();

  const [inputs, setInputs] = useState({
    idStudent: id,
    idProfessor: 0,
    name: '',
    description: '',
    approved: 'proceso',
  });

  const handleChangeInputs = (e) => {
    setInputs({ ...inputs, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    saveRequest({ request: inputs, signOut });
  };

  useEffect(() => {
    if (Object.keys(request).length > 0) {
      history.push('/solicitudes/student/view');
    }
  }, [request, history]);

  return (
    <Fragment>
      <form className="form" onSubmit={handleSubmit}>
        <Grid container spacing={2}>
          <Grid item xs={7}>
            <InputTextForm
              title="Nombre completo"
              description="Nombre complete del estudiante."
              type="text"
              onChange={handleChangeInputs}
              name="name"
              readOnly={true}
              required={false}
              value={`${name} ${surname}`}
            />
          </Grid>
          <Grid item xs={5}>
            <InputTextForm
              title="Carn√©"
              type="text"
              description="Ingresa n√∫mero de carn√©."
              placeholder="1290-18-00000"
              onChange={handleChangeInputs}
              name="carne"
              readOnly={true}
              required={false}
              value={carne}
            />
          </Grid>
        </Grid>
        <Grid container spacing={2}>
          <Grid item xs={8}>
            <Autocomplete
              title="Seleciona a un catedr√°tico"
              placeholder="Buscar"
              items={items}
              onSelect={(e) => setInputs({ ...inputs, idProfessor: e.id })}
            />
          </Grid>
        </Grid>
        <Grid container>
          <Grid item xs={12}>
            <InputTextForm
              title="T√≠tulo del proyecto"
              description="Titulo del proyecto. Los grandes nombres de proyectos son breves y f√°ciles de recordar. ¬øNecesitas inspiraci√≥n?"
              placeholder="Mi impresionante proyecto"
              type="text"
              onChange={handleChangeInputs}
              name="name"
              readOnly={false}
              required={true}
              value={inputs.name}
            />
          </Grid>
        </Grid>
        <InputTextForm
          title="Descripci√≥n del proyecto (opcional)"
          type="text"
          description="Agrega una descripci√≥n a tu proyecto para que sea mas f√°cil la comprension de tu idea. üòá "
          placeholder="Mi impresionante proyecto"
          multiline={true}
          rows={5}
          maxRows={6}
          name="description"
          onChange={handleChangeInputs}
          readOnly={false}
          required={true}
          value={inputs.description}
        />

        <div className="form-action">
          <Button
            type="submit-form"
            text="Enviar solicitud"
            color="primary"
            size="small"
            variant="contained"
            onSubmit={handleSubmit}
            loading={loading}
            action="submit"
          />
          <Button
            name="cancel"
            type="submit-form-normal"
            text="Cancelar"
            size="small"
            variant="outlined"
            disabled={loading}
            action="button"
            onClick={() => history.push(`/solicitudes/student`)}
          />
        </div>
        <AlertError description={message} open={error} setOpen={showInfo} />
      </form>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  id: state.AuthReducer.userInfo.id,
  name: state.AuthReducer.userInfo.name,
  surname: state.AuthReducer.userInfo.surname,
  carne: state.AuthReducer.userInfo.carne,
});

const mapDispatchToProps = (dispatch) => ({
  setData: (data) => dispatch(setData(data)),
  signOut: () => dispatch(signOut()),
});
export default connect(mapStateToProps, mapDispatchToProps)(FormNewRequest);
