import { TextField as MuiTextField } from '@mui/material';
import { styled } from '@mui/styles';
import { Fragment } from 'react';

const TextFieldCustom = styled(MuiTextField)({
  '& label.Mui-focused': {
    color: '#fff',
  },
  '& .MuiInput-underline:after': {
    borderBottomColor: '#fff',
  },
  '& .MuiOutlinedInput-root': {
    '& fieldset': {
      borderColor: '#dbdbdb8c',
    },
    '&:hover fieldset': {
      borderColor: '#41436a',
    },
    '&.Mui-focused fieldset': {
      borderColor: '#fff',
    },
  },
});
const inputStyle = {
  margin: '10px 0px 0px 0px',
  borderRadius: '50px',
  width: '100%',
  color: '#fff',
  '& input ': {
    borderRadius: '50px',
  },
};
const TextField = ({
  title,
  name,
  type,
  onChange,
  value,
  placeholder,
  description,
  required,
  typeInput,
  color,
}) => {
  return (
    <Fragment>
      <div className={`input ${color}`}>
        <label className="input-label">{title}</label>
        <TextFieldCustom
          style={inputStyle}
          variant="outlined"
          name={name}
          type={type}
          onChange={onChange}
          value={value}
          size="small"
          placeholder={placeholder}
          required={required}
        />
        {description && (
          <small className="input-description">{description}</small>
        )}
      </div>
    </Fragment>
  );
};

export default TextField;
