import { useCallback, useState } from 'react';
import { apiLogIn } from '../services/serviceLogin';

const useLogin = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [success, setSuccess] = useState(false);
  const [user, setUser] = useState({});
  const [message, setMessage] = useState('');

  const closeAlert = () => setError(false);

  const LogIn = useCallback(({ credential, idRole }) => {
    setLoading(true);
    apiLogIn({ credential, idRole })
      .then((resp) => {
        setLoading(false);
        setUser(resp.data);
        return true;
      })
      .then((status) => {
        setSuccess(true);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);

        if (err.response) {
          const { data } = err.response;
          setMessage(data.message);
        } else {
          setMessage(
            'Ops! Hubo un problema, verifique si tiene conexi√≥n a internet y vuelva a intentarlo.'
          );
        }
      });
  }, []);
  return {
    loading,
    success,
    error,
    user,
    message,
    LogIn,
    closeAlert,
  };
};

export default useLogin;
