import { useCallback, useState } from 'react';
import {
  apiSaveRequest,
  apiUpdateRequestById,
} from '../services/serviceRequest';

const useRequest = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [success, setSuccess] = useState(false);
  const [requests] = useState([]);
  const [request, setRequest] = useState({});
  const [message, setMessage] = useState('');

  const showInfo = () => setError(false);

  const saveRequest = useCallback(({ request, signOut }) => {
    setLoading(true);
    apiSaveRequest({ request })
      .then((resp) => {
        setLoading(false);
        setRequest(resp);
        setSuccess(true);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);

        if (err.response) {
          const { data } = err.response;
          setMessage(data.message);

          if (err.response.status === 403) {
            signOut();
          }
        }
      });
  }, []);

  const updateRequest = useCallback(({ request, id, signOut }) => {
    setLoading(true);
    apiUpdateRequestById({ info: request, id })
      .then((resp) => {
        setLoading(false);
        setRequest(resp.data);
        setSuccess(true);
      })
      .catch((err) => {
        setError(true);
        setLoading(false);
        if (err.response) {
          setMessage(err.response.data);
          if (err.response.status === 403) {
            signOut();
          }
        }
      });
  }, []);
  return {
    message,
    loading,
    error,
    success,
    requests,
    request,
    saveRequest,
    showInfo,
    updateRequest,
  };
};

export default useRequest;
