import axios from 'axios';

const getENV = () => {
  switch (process.env.REACT_APP_BACKEND_URL) {
    case 'production': {
      return process.env.REACT_APP_BACKEND_PROD_URL;
    }
    case 'dev': {
      return process.env.REACT_APP_BACKEND_DEV_URL;
    }
    default:
      return 'localhost';
  }
};

axios.interceptors.request.use(
  (config) => {
    let user = localStorage.getItem('umgInfo');
    if (user) {
      let info = JSON.parse(user);
      const auth = info ? `Bearer ${info.token}` : '';
      config.headers.common['Authorization'] = auth;
    }
    return config;
  },
  (error) => Promise.reject(error)
);

export const API_REST = getENV();
