import * as Actions from '../pages/Login/actions/auth.actions';

const isAuth = () => {
  try {
    const state = localStorage.getItem('umgInfo');
    const info = JSON.parse(state);
    return info;
  } catch (err) {
    return null;
  }
};

const getRoleStorage = () => {
  try {
    const state = localStorage.getItem('role');
    const role = JSON.parse(state);
    return role;
  } catch (err) {
    return null;
  }
};

const initialState = {
  userInfo: {
    id: isAuth() ? isAuth().id : null,
    name: isAuth() ? isAuth().name : null,
    surname: isAuth() ? isAuth().surname : null,
    carne: isAuth() ? isAuth().carne : null,
    loggedIn: isAuth() ? isAuth().loggedIn : false,
  },

  role: getRoleStorage() ? getRoleStorage() : null,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case Actions.SET_AUTH_INFO:
      return {
        ...state,
        userInfo: {
          id: action.payload.id,
          name: action.payload.name,
          surname: action.payload.surname,
          carne: action.payload.carne,
          loggedIn: action.payload.loggedIn,
        },
      };
    case Actions.SET_ROLE:
      return {
        ...state,
        role: action.payload,
      };

    case Actions.SIGN_OUT: {
      return {
        ...state,
        userInfo: {
          id: null,
          name: null,
          surname: null,
          carne: null,
          loggedIn: false,
        },
        role: null,
      };
    }
    default:
      return {
        ...state,
      };
  }
};
export default reducer;
