import { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { AlertError } from '../../component/alert/alertDanger';
import { Card } from '../../component/card';
import { FormAction } from '../../component/formActions/FormAction';
import { InputText } from '../../component/input';
import TextField from '../../component/input/textField';
import { Main } from '../../component/layout/main';
import useLogin from '../../hooks/useLogin';
import { setAuthInfo, setRole } from './actions/auth.actions';

const LoginPage = ({ setInfoUser, setRole, loggedIn, role }) => {
  const { LogIn, loading, error, message, closeAlert, user, success } =
    useLogin();

  const [inputs, setInputs] = useState({
    email: '',
    password: '',
  });
  const [idRole, setIdRole] = useState('');

  const history = useHistory();
  const onSubmit = (e) => {
    e.preventDefault();
    LogIn({ credential: inputs, idRole });
  };

  const handleChangeInputs = (e) => {
    setInputs({ ...inputs, [e.target.name]: e.target.value });
  };

  useEffect(() => {
    if (success) {
      setInfoUser({ ...user, loggedIn: true });
      setRole(idRole);
    }
  }, [success, user, idRole, setInfoUser, setRole]);

  useEffect(() => {
    if (loggedIn) {
      if (role === 1) {
        history.push('/solicitudes/admin');
      } else if (role === 2) {
        history.push('/solicitudes/teacher');
      } else if (role === 3) {
        history.push('/solicitudes/student');
      }
    }
  }, [loggedIn, role, history]);

  return (
    <Main active={true}>
      <div className="container">
        <div className="wrap">
          <Card intensity="0.1rem" color="#0000008c" fontColor="#fff">
            <div className="card-container">
              <div className="card-header">
                <div className="logo">UMG</div>
              </div>
              <div className="card-body">
                <div className="title">Iniciar sesi√≥n</div>
                <form onSubmit={onSubmit}>
                  <TextField
                    title="Correo"
                    placeholder="example@miumg.edu.gt"
                    description="Ingresa t√∫ correo que te proporciono la universidad."
                    required={true}
                    typeInput="input"
                    type="email"
                    name="email"
                    color="white"
                    value={inputs.email}
                    readOnly={false}
                    onChange={handleChangeInputs}
                  />
                  <TextField
                    title="Contrase√±a"
                    placeholder="***********"
                    required={true}
                    typeInput="input"
                    type="password"
                    name="password"
                    color="white"
                    value={inputs.password}
                    onChange={handleChangeInputs}
                  />
                  <InputText
                    typeInput="select"
                    description="Selecciona si eres estudiante o maestro."
                    onChange={(e) => setIdRole(e.target.value)}
                    required={true}
                    value={idRole}
                    name="role"
                  />

                  <FormAction onSubmit={onSubmit} loading={loading} />
                  <div className="form-footer">
                    <AlertError
                      open={error}
                      description={message}
                      setOpen={closeAlert}
                    />
                  </div>
                </form>
              </div>
              <div className="card-footer"></div>
            </div>
            <div className="background-card"></div>
          </Card>
        </div>
      </div>
    </Main>
  );
};

const mapStateToProps = (state) => ({
  loggedIn: state.AuthReducer.userInfo.loggedIn,
  role: state.AuthReducer.role,
});

const mapDispatchToProps = (dispatch) => ({
  setInfoUser: (infoUser) => dispatch(setAuthInfo(infoUser)),
  setRole: (role) => dispatch(setRole(role)),
});

export default connect(mapStateToProps, mapDispatchToProps)(LoginPage);
