import * as React from 'react';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';

import LinearProgress, {
  linearProgressClasses,
} from '@mui/material/LinearProgress';
import { Tooltip } from '@mui/material';

function sleep(delay = 0) {
  return new Promise((resolve) => {
    setTimeout(resolve, delay);
  });
}
export const ProgressBar = ({ status }) => {
  const [progress, setProgress] = React.useState(0);
  const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
    height: 7,
    borderRadius: 5,
    [`&.${linearProgressClasses.colorPrimary}`]: {
      backgroundColor:
        theme.palette.grey[theme.palette.mode === 'light' ? 200 : 800],
    },
    [`& .${linearProgressClasses.bar}`]: {
      borderRadius: 5,
      backgroundColor:
        status === 'proceso'
          ? '#1a90ff'
          : status === 'aprobado'
          ? '#00cf3b'
          : status === 'rechazado'
          ? 'red'
          : '',
    },
  }));

  React.useEffect(() => {
    const timer = setInterval(() => {
      (async () => {
        await sleep(700); // For demo purposes.

        setProgress((prevProgress) =>
          status === 'proceso'
            ? prevProgress === 50
              ? 50
              : prevProgress + 10
            : status === 'aprobado'
            ? prevProgress === 100
              ? 100
              : prevProgress + 10
            : status === 'rechazado'
            ? prevProgress === 100
              ? 100
              : prevProgress + 10
            : ''
        );
      })();
    }, 1);

    return () => {
      clearInterval(timer);
    };
  });

  return (
    <Tooltip
      arrow
      title={
        status === 'proceso'
          ? 'Tu proyecto esta en proceso, esto puede tardar unas horas o algunos dias.'
          : status === 'aprobado'
          ? 'Tu proyeco ha sido aprobado por el docente. '
          : status === 'rechazado'
          ? 'Lamentablemte tu proyecto ha sido rechazado, ponte en contacto con tu docente para mas informaci√≥n'
          : 'No disponible'
      }
    >
      <Box sx={{ flexGrow: 1 }}>
        <BorderLinearProgress variant="determinate" value={progress} />
      </Box>
    </Tooltip>
  );
};
