import { useEffect, useRef, useState } from 'react';
import { CSSTransition } from 'react-transition-group';
import { itemsNavHeader } from '../../../util/itemsNav';
import { ButtonAvatar } from '../../button/buttonAvatar';
import Card from '@mui/material/Card';
import { Link } from 'react-router-dom';
import { signOut } from '../../../pages/Login/actions/auth.actions';
import { connect } from 'react-redux';
import { useEventListener } from '../../../hooks/useAddEventListener';

const Header = ({ name, surname, signOut }) => {
  const [open, setOpen] = useState(false);
  const [active, setActive] = useState(false);

  const ref = useRef();

  const handleActive = (e) => {
    if (!ref.current.contains(e.target)) {
      setActive(false);
    }
  };

  useEventListener(handleActive);

  useEffect(() => {
    setOpen(true);

    return () => setOpen(false);
  }, []);

  return (
    <CSSTransition in={open} classNames="fade" timeout={500} unmountOnExit>
      <div className="header">
        <nav className="navbar">
          <div className="logo">UMG</div>
          <div className="nav-right">
            <ul>
              {itemsNavHeader.map((item, key) => (
                <li key={key}>
                  <a href={item.to}>{item.title}</a>
                </li>
              ))}
            </ul>
            <div className="submenu-modal" ref={ref}>
              <ButtonAvatar
                active={active}
                onClick={() => setActive(!active)}
                name="button-avatar"
              />
              {active && (
                <Card style={{ position: 'absolute' }}>
                  <div className="nav-right-modal">
                    <Link to="#" onClick={() => setActive(false)}>
                      <div className="settings-header">
                        <strong>{name}</strong>
                        <span>{surname}</span>
                      </div>
                    </Link>
                    <div className="settings-body">
                      <span onClick={() => setActive(false)}>
                        Editar perfile
                      </span>
                      <span onClick={() => setActive(false)}>
                        Establecer estado
                      </span>
                      <span onClick={() => setActive(false)}>Preferencias</span>
                    </div>
                    <div className="settings-footer" onClick={signOut}>
                      <span id="log-out">Cerrar sesi√≥n</span>
                    </div>
                  </div>
                </Card>
              )}
            </div>
          </div>
        </nav>
      </div>
    </CSSTransition>
  );
};

const mapStateToProps = (state) => ({
  name: state.AuthReducer.userInfo.name,
  surname: state.AuthReducer.userInfo.surname,
  carne: state.AuthReducer.userInfo.carne,
});
const mapDispatchToProps = (dispatch) => ({
  signOut: () => dispatch(signOut()),
});

export default connect(mapStateToProps, mapDispatchToProps)(Header);
