import moment from 'moment';
import { Fragment, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { useHistory, useRouteMatch } from 'react-router-dom';
import { CSSTransition } from 'react-transition-group';
import { Button } from '../../component/button/button';
import { Card } from '../../component/card';
import { Main } from '../../component/layout/main';
import { LoadingSpinner } from '../../component/spinner';
import { LabTabs } from '../../component/tab';
import Title from '../../component/title';
import useItems from '../../hooks/useItems';
import { apiGetRequestByIdTeacher } from '../../services/serviceRequest';
import { signOut } from '../Login/actions/auth.actions';

const RequestsToApproved = ({ id, signOut }) => {
  const { items, loading, success } = useItems({
    id,
    serviceApi: apiGetRequestByIdTeacher,
    signOut
  });
  const { path } = useRouteMatch();
  const history = useHistory();

  return (
    <Fragment>
      <div className="lab-container">
        {loading && <LoadingSpinner />}
        {success && (
          <CSSTransition
            classNames="fade"
            in={success}
            unmountOnExit
            timeout={400}
          >
            <div>
              {items
                .filter((item) => item.approved === 'proceso')
                .map((item, key) => (
                  <div style={{ marginTop: '10px' }} key={key}>
                    <Card
                      borderTop={true}
                      sizeBorder={5}
                      colorBorder="#7373ff8a"
                      colorHover="#7373ff"
                      padding="25px 50px"
                      display="block"
                    >
                      <div className="card-body request-item">
                        <div className="info">
                          <div className="title">{item.name}</div>
                          <div className="description">{item.description}</div>
                        </div>
                        <div className="actions">
                          <Button
                            type="normal"
                            text="Revisar"
                            onClick={() =>
                              history.push(`${path}/check/${item.id}`)
                            }
                          />
                          <div className="date">
                            {moment(item.date).format('LLLL')}
                          </div>
                        </div>
                      </div>
                    </Card>
                  </div>
                ))}
            </div>
          </CSSTransition>
        )}
        {(items.length === 0 || items.filter( item => item.approved === 'proceso').length === 0) && (
          <CSSTransition
            in={true}
            classNames="fade"
            unmountOnExit
            timeout={400}
          >
            <div className="empty">
              <div style={{ textAlign: 'center' }}>
                <img
                  src="https://carrerainglesuce.files.wordpress.com/2019/05/papers.gif"
                  alt=""
                  width={200}
                />
                <div className="description">
                  No tienes solucitudes pendientes.
                </div>
              </div>
            </div>
          </CSSTransition>
        )}
      </div>
    </Fragment>
  );
};

const RequestsApproved = ({ id, signOut }) => {
  const { path } = useRouteMatch();
  const history = useHistory();

  const { items, loading, success } = useItems({
    id,
    serviceApi: apiGetRequestByIdTeacher,
    signOut
  });

  return (
    <Fragment>
      <div className="lab-container">
        {loading && <LoadingSpinner />}
        {success && (
          <CSSTransition
            classNames="fade"
            in={success}
            unmountOnExit
            timeout={400}
          >
            <div>
              {items
                .filter((item) => item.approved === 'aprobado')
                .map((item, key) => (
                  <div style={{ marginTop: '10px' }} key={key}>
                    <Card
                      borderTop={true}
                      sizeBorder={5}
                      colorBorder="#52db5969"
                      colorHover="#00af0a"
                      padding="25px 50px"
                      color="#52db5969"
                      display="block"
                    >
                      <div className="card-body request-item">
                        <div className="info">
                          <div className="title">{item.name}</div>
                          <div className="description">{item.description}</div>
                        </div>
                        <div className="actions">
                          <Button
                            color="success"
                            type="normal"
                            text="Ver"
                            variant="outlined"
                            onClick={() =>
                              history.push(`${path}/view/${item.id}`)
                            }
                          />
                          <div className="date">
                            {moment(item.date).format('LLLL')}
                          </div>
                        </div>
                      </div>
                    </Card>
                  </div>
                ))}
            </div>
          </CSSTransition>
        )}
        {success &&
          (items.filter((item) => item.approved === 'aprobado').length === 0 || items.length === 0) && (
            <CSSTransition
              in={true}
              classNames="fade"
              unmountOnExit
              timeout={400}
            >
              <div className="empty">
                <div style={{ textAlign: 'center' }}>
                  <img
                    src="https://carrerainglesuce.files.wordpress.com/2019/05/papers.gif"
                    alt=""
                    width={200}
                  />
                  <div className="description">
                    No tienes solucitudes aprobadas.
                  </div>
                </div>
              </div>
            </CSSTransition>
          )}
      </div>
    </Fragment>
  );
};

const RejectedRequests = ({ id, signOut }) => {
  const { path } = useRouteMatch();
  const history = useHistory();
  const { items, loading, success } = useItems({
    id,
    serviceApi: apiGetRequestByIdTeacher,
    signOut
  });
  return (
    <Fragment>
      <div className="lab-container">
        {loading && <LoadingSpinner />}
        {success && (
          <CSSTransition
            classNames="fade"
            in={success}
            unmountOnExit
            timeout={400}
          >
            <div>
              {items
                .filter((item) => item.approved === 'rechazado')
                .map((item, key) => (
                  <div style={{ marginTop: '10px' }} key={key}>
                    <Card
                      borderTop={true}
                      sizeBorder={5}
                      colorBorder="#d1676763"
                      colorHover="#d16767"
                      padding="25px 50px"
                      color="#d1676763"
                      display="block"
                    >
                      <div className="card-body request-item">
                        <div className="info">
                          <div className="title">{item.name}</div>
                          <div className="description">{item.description}</div>
                        </div>
                        <div className="actions">
                          <Button
                            type="normal"
                            color="secondary"
                            text="Ver"
                            onClick={() =>
                              history.push(`${path}/view/${item.id}`)
                            }
                          />
                          <div className="date">
                            {moment(item.date).format('LLLL')}
                          </div>
                        </div>
                      </div>
                    </Card>
                  </div>
                ))}
            </div>
          </CSSTransition>
        )}
        {success &&
          items.filter((item) => item.approved === 'rechazado').length ===
            0 && (
            <CSSTransition
              in={true}
              classNames="fade"
              unmountOnExit
              timeout={400}
            >
              <div className="empty">
                <div style={{ textAlign: 'center' }}>
                  <img
                    src="https://carrerainglesuce.files.wordpress.com/2019/05/papers.gif"
                    alt=""
                    width={200}
                  />
                  <div className="description">
                    No tienes solucitudes rechazadas.
                  </div>
                </div>
              </div>
            </CSSTransition>
          )}
      </div>
    </Fragment>
  );
};

const Home = ({ id, loading, setLoading, username, setId, signOut }) => {
  const header = [
    'Solicitudes para revisar',
    'Solicitudes aprobadas',
    'Solicitudes rechazadas',
  ];
  const content = [
    <RequestsToApproved id={id} signOut={signOut}/>,
    <RequestsApproved id={id} signOut={signOut} />,
    <RejectedRequests id={id} signOut={signOut} />,
  ];
  return (
    <Main limit="limit">
      <Title title="Solicitudes de adjudicaci√≥n" />
      <LabTabs items={header} content={content} />
    </Main>
  );
};

const mapStateToProps = (state) => ({
  id: state.AuthReducer.userInfo.id,
});

const mapDispatchToProps = (dispatch) => ({
  signOut: () => dispatch(signOut())
});

export default connect(mapStateToProps, mapDispatchToProps)(Home);
