import React, { Fragment } from 'react';

import * as Material from '@mui/material';

import { AvatarUser } from '../avatar/index';
import { styled } from '@mui/system';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';

const btnMenu = {
  margin: 'none',
};
const IconButtonCustom = styled(Material.Button)(() => ({
  backgroundColor: '#f0f8ff0a',
  boxShadow: 'none',
  padding: '2px 5px',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  '& span ': {
    margin: '8px',
  },

  '&:hover': {
    color: '#0a4769',
    backgroundColor: 'white',
  },
}));

export const ButtonAvatar = ({ active, onClick, name }) => {
  return (
    <Fragment>
      <IconButtonCustom
        name={name}
        style={btnMenu}
        variant="contained"
        endIcon={<ArrowDropDownIcon />}
        size="small"
        className={active ? 'button-active' : ''}
        onClick={onClick}
      >
        <AvatarUser type="header-profile" />
      </IconButtonCustom>
    </Fragment>
  );
};
