import { Main } from '../../component/layout/main';
import FormNewRequest from '../../component/form/formNewRequest';
import { Grid } from '@mui/material';
import imageNewRequest from '../../assets/images/new-request.gif';
import { useEffect } from 'react';
import { useTeacher } from '../../hooks/useTeacher';
import { signOut } from '../Login/actions/auth.actions';
import { connect } from 'react-redux';

const NewRequests = ({ signOut }) => {
  const { teachers, getTeachers } = useTeacher();
  useEffect(() => {
    getTeachers({ signOut });
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  return (
    <Main limit="limit" back={true}>
      <div className="grid-form">
        <div className="grid-item">
          <div className="grid-container">
            <div className="info">
              <img
                src={imageNewRequest}
                alt="default"
                width={200}
                height={200}
              />
              <div className="info-container">
                <span className="title">
                  <strong> Crear una nueva solicitud de proyecto. </strong>{' '}
                </span>
                <p>
                  Realize una solicitud para la revision de su proyecto. Suerte!
                  ü§©
                </p>
              </div>
            </div>
          </div>
        </div>
        <div className="grid-item">
          <div className="grid-container">
            <div className="grid-container">
              <div className="grid-header">
                <h3 className="title">Nueva solicitud</h3>
              </div>
              <FormNewRequest items={teachers} />
            </div>
          </div>
        </div>
      </div>
    </Main>
  );
};

const mapDispatchToProps = (dispatch) => ({
  signOut: () => dispatch(signOut()),
});

export default connect(null, mapDispatchToProps)(NewRequests);
