import { useParams } from 'react-router-dom';
import { Main } from '../../component/layout/main';
import Title from '../../component/title';
import Face from '../../assets/images/faces.gif';
import { apiGetRequestById } from '../../services/serviceRequest';
import useItem from '../../hooks/useItem';
import { LoadingSpinner } from '../../component/spinner';
import { CSSTransition } from 'react-transition-group';
import moment from 'moment';

const ViewRequest = () => {
  const { id } = useParams();
  const { item, loading, success } = useItem({
    id,
    serviceApi: apiGetRequestById,
  });
  return (
    <Main limit="limit">
      <Title title={`Vista de solicitud No. ${id}`} marginBottom="0px" back={true}/>
      {loading && <LoadingSpinner />}
      {success && (
        <CSSTransition
          in={success}
          timeout={600}
          unmountOnExit
          classNames="fade"
        >
          <div className="container-page">
            <div className="request">
              <div className="request-wrap">
                <div className="request-container">
                  <div className="request-info">
                    <div className="date">
                      {moment(item.date).format('LLLL')}{' '}
                    </div>
                    <div className="title">
                      <h3>{item.name}</h3>
                    </div>
                    <div className="request-info-body">
                      <div className="info-student">
                        <div className="name">
                          Autor{' '}
                          <strong>
                            <i>
                              {item.name_student} {item.surname_student}{' '}
                            </i>
                          </strong>
                        </div>
                        <div className="carne">{item.carne_student}</div>
                        <div className="career">
                          Estudiante de ingenier√≠a en sistemas de informaci√≥n y
                          ciencias de la computaci√≥n.
                        </div>
                      </div>
                      <div className="info-sede">La Antigua Guatemala.</div>
                    </div>
                  </div>
                  <div className="request-avatar">
                    <img src={Face} alt="face" width={400} />
                  </div>
                </div>
                <div className="request-body">
                  <div className="title">
                    <h3>Descripci√≥n</h3>
                  </div>
                  <div className="description">
                    <p>{item.description}</p>
                  </div>
                </div>
                <div className="request-footer">
                  <span>Universidad Mariano G√°lvez de Guatemala</span>
                </div>
              </div>
            </div>
          </div>
        </CSSTransition>
      )}
    </Main>
  );
};

export default ViewRequest;
